import { useState } from 'react';
import { CartIcon } from '../../../spiritIcons';
import { styled } from "@mui/system";
import { Popover, Grid, Typography } from "@mui/material/";

const CartIconStyled = styled(CartIcon)`
    &:hover {
        cursor: pointer;
    }
`


const Cart = (props) => {
  const [anchorEl, setAnchorEl] = useState(null);
//   const [subtotal, setSubtotal] = useState(0);

  const getCartLocal = () => {
      return JSON.parse(localStorage.getItem('cartItems'))
  }
  
  const calculateSubtotal = () => {
      const cart = JSON.parse(localStorage.getItem('cartItems'));
      if(cart){
          const total = cart.reduce((accumulator, current) => accumulator + current.price * current.quantity, 0);
          return total;
      }
      return 0;
  }

  calculateSubtotal();

  const handleCartClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCartClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const GridWrapper = styled(Grid)`
    max-width: 418px;
    min-width: 200px;
    padding: 20px
  `;

  const WineImage = styled('img')`
    max-height: 126px;
    max-width: 50px;
  `

  return (
    <>
      <CartIconStyled onClick={(e) => handleCartClick(e)} />
      <Popover
        open={open}
        onClose={handleCartClose}
        anchorEl={anchorEl}
        elevation={1}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
      >
        <GridWrapper container>
          <Grid container item spacing={2}>
            {getCartLocal() ? (
              getCartLocal().map((item) => (
                <Grid item container direction="row" key={item.id}>
                  <Grid item container xs={3} justifyContent="center">
                    <WineImage src={item.image} />
                  </Grid>
                  <Grid item container xs={9} direction="column">
                    <Grid item>
                      <Typography variant="body1" fontSize="20px">
                        {item.name}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="13px">
                        Price: {item.price}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="13px">
                        Quantity: {item.quantity}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize="21px">
                        Subtotal: {item.price * item.quantity}$
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              ))
            ) : (
              <Grid container item>
                <Grid item>
                  <Typography variant="body1" fontSize="24px" textAlign="center">
                    Your cart is empty, please add products
                  </Typography>
                </Grid>
              </Grid>
            )}
          </Grid>
          <Grid item container justifyContent="flex-end" mt="50px">
            <Typography variant="body1" fontSize="25px">
              Subtotal: {calculateSubtotal()}$
            </Typography>
          </Grid>
        </GridWrapper>
      </Popover>
    </>
  );
}

export default Cart;