import { useState } from 'react';
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import {CutleryIcon, TemperatureIcon, IncreaseIcon, DecreaseIcon} from '../../spiritIcons';
import SpiritButton from '../SpiritButton';

const NavbarSpace = styled('div')`
    height: 75px;
    width: '100%';
`

const WineImage = styled('img')`
    max-height: 745px;
`

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
`

const DetailsDivider = styled('div')`
    border-bottom: solid 1px #707070;
    width: 100%;
    padding-top: 30px;
`

const CardContainer = styled('div')`
    // max-height: 394px;
    // max-width: 600px;
    color: #fbfbfb;
`

const RecommendCard = styled('div')`
    background-color: #292929;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
`
const CardImage = styled('img')`
    position: absolute;
    width: 100%;
    display: inline-block;
    height: 100%;
`

const CardLine = styled('div')`
    width: 39px;
    height: 4px;
    background-color: #fff;
`

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`


function WinePage(props){
    const [count, setCount] = useState(1);

    const handleIncreaseButtonClick = () => {
        setCount(count + 1)
    }

    const handleDecreaseeButtonClick = () => {
        if(count !== 1){
            setCount(count - 1)
        }
    }

    const { 
        alcoholPercent, 
        id,
        image,
        name,
        type,
        region,
        variety,
        year,
        description,
        price,
        vendor
    } = props.location.state

    const handleAddToCartButton = () => {
        const currentCartItem = {
            name,
            price,
            type,
            image,
            id,
            quantity: 1
        }
        const webStorageCart = localStorage.getItem('cartItems');
 
        if (webStorageCart) {
          const cartArr = JSON.parse(webStorageCart);
          const match = cartArr.filter((item) => item.id === currentCartItem.id);
          if (Array.isArray(match) && match.length) {
            const rest = cartArr.filter(
              (item) => item.id !== currentCartItem.id
            );
            match[0].quantity += count;
            localStorage.setItem(
              "cartItems",
              JSON.stringify([...rest, match[0]])
            );
          } else {
            localStorage.setItem('cartItems', JSON.stringify([...cartArr, currentCartItem]));
          }
        } else {
          localStorage.setItem("cartItems", JSON.stringify([currentCartItem]));
        }
        setCount(1);
    }


    return (
        <>
            <NavbarSpace></NavbarSpace>
            <Navbar fixed />
            <ContentWrapper>
                <Grid container>
                    <Grid item container direction="row"  pt="117px">
                        <Grid item container xs={5} justifyContent="center">
                            <WineImage src={image}></WineImage>
                        </Grid>
                        <Grid item container xs={7} direction="column">
                            <Typography variant="body1" fontSize="25px">{type}</Typography>
                            <Typography variant="body1" fontSize="25px" pt="12px">From {region} (made in {year})</Typography>
                            <Typography variant="h2" fontSize="70px" pt="12px">{name}</Typography>
                            <Grid item container direction="row" mt="46px">
                                <Grid item xs={2}>
                                    <Box sx={{borderTop: 'solid 2px #202020', mt: "10px", maxWidth: '81px'}} />
                                </Grid>
                                <Grid item container xs={10} direction="column" >
                                    <Grid item>
                                        <Typography variant="body1" fontSize="20px">{description}</Typography>
                                        <Typography variant="body1" fontSize="45px" mt="43px">${price}</Typography>
                                    </Grid>
                                    <Grid item container direction="row" mt="46px" >
                                        <Grid item container direction="row" xs={3} alignItems="center" justifyContent="center">
                                            <Grid item xs={3}>
                                                <IncreaseIcon onClick={()=> handleIncreaseButtonClick()}/>
                                            </Grid>
                                            <Grid item xs={6} sx={{ textAlign: 'center' }}>{count}</Grid>
                                            <Grid item xs={3}>
                                                <DecreaseIcon onClick={() => handleDecreaseeButtonClick()}/>
                                            </Grid>
                                        </Grid>
                                        <Grid item xs={9} pl="50px">
                                            <SpiritButton onClick={() => handleAddToCartButton()}>Add to Cart</SpiritButton>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item container direction="column" mt="156px">
                        <Grid item container direction="row" justifyContent="center">
                            <Typography variant="h3" fontSize="50px">Wine Details.</Typography>
                        </Grid>
                        <Grid item container direction="row" spacing={3} mt="76px" >
                            <Grid item container xs={6} direction="column">
                                <Grid item container>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">Color</Typography>
                                    </Grid>
                                    <Grid item sx={{flexGrow: 1}}>
                                        <DetailsDivider />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">{type.split(" ")[0]}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item container mt="42px">
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">Winery</Typography>
                                    </Grid>
                                    <Grid item sx={{flexGrow: 1}}>
                                        <DetailsDivider />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">{vendor}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item container mt="42px">
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">Type</Typography>
                                    </Grid>
                                    <Grid item sx={{flexGrow: 1}}>
                                        <DetailsDivider />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">{type.split(" ")[1]}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item container mt="42px">
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">Alcohol</Typography>
                                    </Grid>
                                    <Grid item sx={{flexGrow: 1}}>
                                        <DetailsDivider />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">{alcoholPercent}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item container mt="42px">
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">Region</Typography>
                                    </Grid>
                                    <Grid item sx={{flexGrow: 1}}>
                                        <DetailsDivider />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">{region}</Typography>
                                    </Grid>
                                </Grid>
                                <Grid item container mt="42px">
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">Variety</Typography>
                                    </Grid>
                                    <Grid item sx={{flexGrow: 1}}>
                                        <DetailsDivider />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" fontSize="25px">{variety}</Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item container xs={6}>
                                <RecommendCard>
                                    <CardContainer>
                                        <CardImage src="/images/abstract-lines-transparent-png.png" style={{opacity: 0.25}}>
                                        </CardImage>
                                        <Grid container>
                                            <Grid item container justifyContent="center" pt="22px">
                                                <Typography variant="body1" fontSize="25px">We Recommend</Typography>
                                            </Grid>
                                            <Grid item container justifyContent="center" pt="29px">
                                                <CardLine />
                                            </Grid>
                                            <Grid item container alignItems="center" pt="80px">
                                                <Grid item container xs={6} direction="column" alignItems="center" >
                                                    <CutleryIcon sx={{width: '45px', height: '45px'}}/>
                                                    <Typography variant="body1" fontSize="20px" lineHeight="1.75">This wine goes well with</Typography>
                                                    <Typography variant="body1" fontSize="14px">Cheese, French Bread, Meat on stove</Typography>
                                                </Grid>
                                                <Grid item container xs={6} direction="column" alignItems="center" >
                                                    <TemperatureIcon sx={{width: '45px', height: '45px'}} />
                                                    <Typography variant="body1" fontSize="20px" lineHeight="1.75">Serving Temperature</Typography>
                                                    <Typography variant="body1" fontSize="14px">15-16 C</Typography>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </CardContainer>
                                </RecommendCard>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </ContentWrapper>
            <FooterWrapper>
                <ContentWrapper>
                    <Footer></Footer>
                </ContentWrapper>
            </FooterWrapper>
        </>
    )
}

export default WinePage;